.header {
    --header-height: 100px;
    font-weight: 600;

    .wrapper {
        align-items: center;
        display: flex;
        justify-content: space-between;
        padding: 0 var(--spacing-sm);
        height: var(--header-height);
    }
    position: sticky;
    top: 0;
    z-index: 10;
    transition: transform 0.4s $ease-out-expo;

    &::before {
        background-color: var(--color-black);
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }

    @media screen and (min-width: $breakpoint-lg) {
        padding: 0 var(--spacing-sm);
    }

    .header-is-hidden.is-scrolling-down:not(.nav-is-active) & {
        transform: translateY(-100%) translateY(4px);
    }
}

.header__brand {
    position: relative;
    font-weight: 900;
    font-size: typo(25px, 40px);

    z-index: 10;
    font-family: var(--font-primary);
    color: #444;
    svg {
        color: var(--color-primary);
        margin-right: 20px;
    }
}

// Mettre ton .header__toggle ici

.header__toggle {
    --bar-height: 8px;
    --bar-spacer: 6px;
    --bar-width: 50px;

    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0;
    position: relative;
    width: var(--bar-width);
    height: 50px;
    z-index: 10;

    span {
        background: var(--color-black);
        border-radius: 3px;
        height: var(--bar-height);
        width: 100%;
        position: absolute;
        top: calc(50% - var(--bar-height) / 2);
        left: calc(50% - var(--bar-width) / 2);
        transition: 0.3s all ease-out;

        &:nth-child(1) {
            top: calc(
                50% - var(--bar-height) / 2 - var(--bar-height) -
                    var(--bar-spacer)
            );
        }

        &:nth-child(3) {
            top: calc(
                50% - var(--bar-height) / 2 + var(--bar-height) +
                    var(--bar-spacer)
            );
        }
    }

    .nav-is-active & {
        span {
            top: calc(50% - var(--bar-height) / 2);
            &:nth-child(1) {
                transform: rotate(45deg);
            }
            &:nth-child(2) {
                transform: scaleX(0);
            }
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
        }
    }
    @media screen and (min-width: $breakpoint-md) {
        display: none;
    }
}

.nav-primary {
    background-color: var(--color-bg-primary);
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    padding-top: var(--header-height);
    display: flex;
    align-items: center;
    transform: translateX(-100%);
    transition: transform 0.6s $ease-out-expo;

    .nav-is-active & {
        transform: translateX(0);
    }

    @media screen and (min-width: $breakpoint-md) {
        display: block;
        background: unset;
        position: relative;
        height: auto;
        width: auto;
        padding-top: 0;
        transform: translateX(0);
    }

    ul {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 40px;
        list-style: none;
        padding: 0;
        margin: 0;
        width: 100%;
        transform: translateX(-70%);
        transition: transform 1.2s $ease-out-expo;

        .nav-is-active & {
            transform: translateX(0);
        }

        a {
            color: var(--color-white);
            display: block;
            text-align: center;
            text-transform: uppercase;
            font-size: typo(14px);
        }
    }

    @media screen and (min-width: $breakpoint-md) {
        display: block;
        background: unset;
        position: static;
        height: auto;
        width: auto;
        overflow-y: visible;
        padding-top: 0;
        transform: translateY(0);
        z-index: 2;

        ul {
            flex-direction: row;
            transform: translateY(0);
        }
    }
}

.nav-primary__item {
    text-transform: uppercase;
    position: relative;

    &:hover {
        color: var(--color-white);
    }

    @media screen and (min-width: $breakpoint-md) {
        position: relative;
        text-transform: none;

        &::before {
            display: block;
            position: absolute;
            top: 100%;
            left: 0;
            height: 2px;
            width: 100%;
            content: '';
            background-color: black;
            transform: scaleX(0);
            transition: 0.3s all;
        }

        &:hover {
            text-decoration: none;
            &::before {
                transform: scaleX(1);
            }
        }
    }
}

.nav-is-active body {
    overflow: hidden;
    @media screen and (min-width: $breakpoint-md) {
        overflow: visible;
    }
}
